<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogFormVisible"
    width="500px"
    @close="close"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.value" disabled></el-input>
      </el-form-item>
      <el-form-item v-if="title === ''" label="" prop="newValue">
        <el-radio v-model="form.newValue" label=""></el-radio>
        <el-radio v-model="form.newValue" label=""></el-radio>
      </el-form-item>
      <el-form-item v-else-if="title !== ''" label="" prop="newValue">
        <el-input v-model="form.newValue" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item v-else label="" prop="phone">
        <el-input
          v-model="form.phone"
          autocomplete="off"
          maxlength="11"
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="save"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import { doEdit } from "@/api/roleManagement";
  import { isPhone } from "@/utils/validate";

  export default {
    name: "RoleManagementEdit",
    props: {
      mid: {
        type: [Number, String],
        default: 9999,
      },
    },
    data() {
      const validatePhone = (rule, value, callback) => {
        if (!isPhone(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      return {
        form: {
          id: "",
          value: "",
          newValue: "",
          title: "",
          phone: "",
        },
        rules: {
          newValue: [
            { required: true, trigger: "blur", message: "" },
          ],
          phone: [
            { required: true, trigger: "blur", message: "" },
            { validator: validatePhone, trigger: "blur" },
          ],
        },
        title: "",
        dialogFormVisible: false,
      };
    },
    created() {},
    methods: {
      showEdit(row) {
        this.form.title = this.title = row.type;
        this.form.id = this.mid;
        this.form.value = row.value;
        this.dialogFormVisible = true;
      },
      close() {
        this.$refs["form"].resetFields();
        this.form = this.$options.data().form;
        this.dialogFormVisible = false;
      },
      save() {
        this.$refs["form"].validate(async (valid) => {
          if (valid) {
            const msg = await doEdit(this.form);

            if (msg === 'success') {
              this.$baseMessage("", "success");
            } else {
              this.$baseMessage(msg, "error");
            }

            this.$emit("fetch-data");
            this.close();
          } else {
            return false;
          }
        });
      },
    },
  };
</script>
