<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogFormVisible"
    width="500px"
    @close="close"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="" prop="managerId">
        <el-input v-model="form.managerId" autocomplete="off" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="managerName">
        <el-input v-model="form.managerName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="managerSex">
        <el-radio v-model="form.managerSex" label=""></el-radio>
        <el-radio v-model="form.managerSex" label=""></el-radio>
      </el-form-item>
      <el-form-item label="" prop="managerIsBoss" >
        <el-radio-group v-model="form.managerIsBoss">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="save"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import { doEditMan } from "@/api/userManagement";

  export default {
    name: "RoleManagementEdit",
    props: {
      maxListNum: {
        type: [Number, String],
        default: 9999,
      },
    },
    data() {
      return {
        form: {
          id: "",
        },
        rules: {
          managerIsBoss: [
            { required: true, trigger: "blur", message: "" },
          ],
          managerName: [{ required: true, trigger: "blur", message: "" }],
          managerSex: [{ required: true, trigger: "blur", message: "" }],
        },
        title: "",
        dialogFormVisible: false,
      };
    },
    created() {},
    methods: {
      showEdit(row) {
        if (!row) {
          this.title = "";
          this.form.managerId = this.maxListNum;
        } else {
          this.title = "";
          this.form = Object.assign({}, row);

          if (this.form.managerIsBoss === 0){
            this.form.managerIsBoss=''
          }else {
            this.form.managerIsBoss=''
          }
          console.log(this.form)
          // if (this.form.permission === "manager") {
          //   this.form.permission = "";
          // }
          // if (this.form.permission === "boss") {
          //   this.form.permission = "";
          // }
        }
        this.dialogFormVisible = true;
      },
      close() {
        this.$refs["form"].resetFields();
        this.form = this.$options.data().form;
        this.dialogFormVisible = false;
      },
      save() {
        this.$refs["form"].validate(async (valid) => {
          if (valid) {
            if (this.form.managerIsBoss === "") {
              this.form.managerIsBoss = 0;
            }
            if (this.form.managerIsBoss === "") {
              this.form.managerIsBoss = 1;
            }

            this.form.managerPassword='111111'

            console.log(this.form);
            const  msg  = await doEditMan(this.form);
            if (msg==='success'){
              this.$baseMessage("", "success");
            }else {
              this.$baseMessage(msg, "error");
            }
            this.$emit("fetch-data");
            this.close();
          } else {
            return false;
          }
        });
      },
    },
  };
</script>
