<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogFormVisible"
    width="500px"
    @close="close"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="" prop="movieId" v-if="title == ''">
        <el-input v-model="form.movieId" autocomplete="off" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="movieName">
        <el-input v-model="form.movieName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="moviePicture"
        class="movie-picture-upload"
      >
        <el-upload
          class="avatar-uploader"
          :action="action"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :accept="imageFormats"
        >
          <img
            v-if="form.moviePicture"
            :src="form.moviePicture"
            class="avatar"
          />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="movieTrailer">
        <el-link
          type="info"
          :href="form.movieTrailer"
          target="_blank"
          v-if="form.movieTrailer"
          ></el-link
        >
        <el-upload
          class="upload-demo"
          ref="upload"
          :action="action"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :multiple="false"
          :limit="1"
          :accept="videoFormats"
          :on-success="handleTrailerSuccess"
          :file-list="fileList"
          :auto-upload="false"
          :on-exceed="handleExceed"
        >
          <el-button slot="trigger" size="small" type="primary"
            ></el-button
          >
          <el-button
            style="margin-left: 10px"
            size="small"
            type="success"
            @click="submitUpload"
            ></el-button
          >
          <div slot="tip" class="el-upload__tip">
            30mb
          </div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="movieDirector">
        <el-input v-model="form.movieDirector" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="movieActor">
        <el-input v-model="form.movieActor" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="movieTime">
        <el-input v-model.number="form.movieTime" autocomplete="off">
          <template slot="append"></template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="movieReleaseTime">
        <el-date-picker
          v-model="form.movieReleaseTime"
          align="right"
          type="datetime"
          format="yyyy-MM-dd"
          placeholder=""
          value-format="yyyy-MM-dd"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="movieType">
        <el-select v-model="form.movieType" multiple placeholder="">
          <el-option
            v-for="item in types"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="movieDes">
        <el-input
          type="textarea"
          placeholder=""
          v-model="form.movieDes"
          maxlength="500"
          show-word-limit
          :clearable="true"
          :autosize="true"
        >
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="save"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { doEdit, addMovie } from "@/api/MovieManagement";
import {baseURL, tokenName} from "@/config/settings";
export default {
  name: "MovieManagementEdit",
  data() {
    return {
      imageFormats:
        ".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF",
      videoFormats:
        ".mp4,.mov,.avi,.wmv,.3gp,.mkv,.flv,.MP4,.MOV,.AVI,.WMV,.3GP,.MKV,.FLV",
      imageUrl: "",
      fileList: [],
      action: baseURL + "/movie/upload",
      form: {
        moviePicture:"",
        movieTrailer: null,
      },
      rules: {
        movieName: [{ required: true, message: "" }],
        movieDirector: [{ required: true, message: "" }],
        movieActor: [{ required: true, message: "" }],
        movieType: [{ required: true, message: "" }],
        movieTime: [{ required: true, type: "number", message: "" }],
        moviePicture: [{ required: true, message: "" }],
        movieReleaseTime: [{ required: true, message: "" }],
        movieDes: [{ required: true, message: "" }],
        movieTrailer: [{ required: true, message: "" }],
      },
      types: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
      title: "",
      dialogFormVisible: false,
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
        ],
      },
    };
  },
  created() {},
  methods: {
    handleAvatarSuccess(res, file) {
      this.form.moviePicture = res;
      console.log("=============");
      console.log(this.form.moviePicture);
    },
    handleTrailerSuccess(res, file) {
      this.form.movieTrailer = res;
      console.log(this.form.movieTrailer);
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 4;
      if (!isLt2M) {
        this.$message.error(" 4MB!");
      }
      return isLt2M;
    },
    showEdit(row) {
      if (!row) {
        this.title = "";
      } else {
        this.title = "";
        this.form = Object.assign({}, row);
        const date = new Date(this.form.movieReleaseTime);
        this.form.movieTime = parseInt(this.form.movieTime.replace("", ""));
        this.form.movieReleaseTime =
          date.getFullYear() +
          "-" +
          (date.getMonth() + 1) +
          "-" +
          date.getDate();
      }
      this.dialogFormVisible = true;
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    close() {
      this.$refs["form"].resetFields();
      this.form = this.$options.data().form;
      this.form.moviePicture = "";
      this.$refs.upload.clearFiles();
      this.dialogFormVisible = false;
    },
    save() {
      this.$refs["form"].validate(async (valid) => {
        if (valid) {
          let msg='';
          this.form.movieType = this.form.movieType.join("/");
          this.form.movieTime = this.form.movieTime + "";
          if (this.title == "") {
            msg=  await addMovie(this.form);
          } else if (this.title == "") {
            msg= await doEdit(this.form);
          }
          if (msg==='success'){
            this.$baseMessage("", msg);
          }else if (msg === 'error'){
            this.$baseMessage("", msg);
          }
          this.$emit("fetch-data");
          this.close();
        } else {
          return false;
        }
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
      this.form.movieTrailer = null;
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 1  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
  },
};
</script>

<style>
.el-form-item__error {
  color: #f56c6c;
  font-size: 12px;
  line-height: 1;
  padding-top: 4px;
  position: relative;
  top: 100%;
  left: 0;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 106px;
  height: 145px;
  display: block;
}
.el-textarea .el-input__count {
  bottom: unset !important;
}
</style>