<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogFormVisible"
    width="500px"
    @close="close"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="">
        <el-input v-model.trim="form.customerId" autocomplete="off" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="customerName">
        <el-input v-model.trim="form.customerName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="customerPassword">
        <el-input
          v-model.trim="form.customerPassword"
          type="password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="customerPhone">
        <el-input v-model.trim="form.customerPhone" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="save"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import { doEditCus } from "@/api/userManagement";
  import { isPhone } from "@/utils/validate";
  export default {
    name: "UserManagementEdit",
    data() {
      const validatePhone = (rule, value, callback) => {
        if (!isPhone(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      };
      return {
        form: {
          customerId: "",
          customerName: "",
          customerPassword: "",
          customerPhone: "",
        },
        rules: {
          customerName: [
            { required: true, trigger: "blur", message: "" },
          ],
          customerPassword: [
            { required: true, trigger: "blur", message: "" },
          ],
          customerPhone: [
            { required: true, trigger: "blur", message: "" },
            { validator: validatePhone, trigger: "blur" },
          ],
        },
        title: "",
        dialogFormVisible: false,
      };
    },
    created() {},
    methods: {
      showEdit(row) {
        console.log(row)
        if (!row) {
          this.title = "";
        } else {
          this.title = "";
          this.form = Object.assign({}, row);
        }
        this.dialogFormVisible = true;
      },
      close() {
        this.$refs["form"].resetFields();
        this.form = this.$options.data().form;
        this.dialogFormVisible = false;
      },
      save() {
        this.$refs["form"].validate(async (valid) => {
          if (valid) {
            const msg = await doEditCus(this.form);
              if (msg==='success'){
                  this.$baseMessage("", "success");
              }else {
                  this.$baseMessage(msg, "error");
              }
            this.$emit("fetch-data");
            this.close();
          } else {
            return false;
          }
        });
      },
    },
  };
</script>
